import React, { useEffect} from "react";
import {urls} from './Apis'


export default function InicioLineas(){

    let IdLinea=null, jsonLineas=[];
    let UbicGraf=[/*{'id':null, 'circle':null, 'punto':null}*/]//geolocalizacion
    let map, directionsRenderer, directionsService;//mapa
    let idProceso, MostrarMicros=false, cant=0;//micros
    let tiempoSolicitud, SegUbicInact;
 
    useEffect(()=>{
        initMap()
        const sel=document.getElementById('select')
        fetch(urls.Lineas)
            .then(response => response.json())
            .then(json => {
                json.forEach(elem => {
                    let option = document.createElement("option");
                    option.text = "Linea "+elem.name+" "+elem.color;
                    option.value = elem.id;
                    sel.appendChild(option)
                });
                jsonLineas=json
            })
            .catch(err => {console.log(err);})

            document.getElementById("inpMilsegSolic").value=100
            document.getElementById("inpSegInact").value="60*60*24*30*3"
   }, []);

    function initMap() {
        map = new window.google.maps.Map(document.getElementById("map"), {
            center: { lat: -17.78443767407429, lng: -63.18189476666437 },
            zoom: 10,
        });
        directionsService = new window.google.maps.DirectionsService();
        directionsRenderer = new window.google.maps.DirectionsRenderer({
            //draggable: true,
            map,
        });

        //CrearUbicaciones()
    }

    function EliminarUbicacionDelMApa(indice) {
        UbicGraf[indice].circle.setMap(null);
        UbicGraf[indice].punto.setMap(null);
        UbicGraf.splice(indice,1)
    }

    async function ActualizarUbicaciones(){
        await fetch(urls.Ubicaciones)
            .then(response => response.json()) 
            .then(async json => {
                json.forEach(async ubic => {
                    if(ubic.idlinea==IdLinea){
                        if(/*!ubic.online || */(Date.now()-ubic.timestamp>100*SegUbicInact)){
                            //await DetenerUbicacionOnline(ubic.idubicacion)
                            let indice = UbicGraf.findIndex(element => element.idubicacion == ubic.idubicacion);
                            if(indice != -1){
                                EliminarUbicacionDelMApa(indice)
                            }
                        }
                        else
                        {
                            let indice = UbicGraf.findIndex(element => element.idubicacion == ubic.idubicacion);
                            if(indice == -1){
                                UbicGraf.push(
                                    {
                                        'idubicacion':ubic.idubicacion,
                                        'circle':new window.google.maps.Circle({
                                            // center: {lat: ubic.latitude, lng: ubic.longitude},
                                            // radius: ubic.accuracy/2,
                                            strokeColor: "black",
                                            strokeOpacity: 0.9,
                                            strokeWeight: 0.2,
                                            fillColor: "blue",
                                            fillOpacity: 0.1,
                                            map,
                                        }),
                                        'punto':new window.google.maps.Marker({
                                            //position: {lat: ubic.latitude, lng: ubic.longitude},
                                            icon: {
                                                path: window.google.maps.SymbolPath.CIRCLE,
                                                scale: 10,
                                            },
                                            map: map,
                                        })
                                    }
                                )
                                indice=UbicGraf.length-1
                            }
                            const pos = {lat:ubic.latitude, lng:ubic.longitude};
                            UbicGraf[indice].circle.setCenter(pos)
                            UbicGraf[indice].circle.setRadius(ubic.accuracy/2)
                            UbicGraf[indice].punto.setPosition(pos)
                            //map.setCenter(pos);
                        }
    
                    }
                });
                //console.log("Solicitud "+(cant)+" OK")
            })
            .catch(async err => {
                console.log("Solicitud "+(cant)+" ERROR ", err)
            }
        )
    }

    async function DetenerUbicacionOnline(id){
        var datos = {
            "id": id,
            "online": false
        }
        await fetch(
            urls.Ubicaciones+'/'+datos.id 
            ,{
                method: 'PUT',
                body: JSON.stringify(datos),
                headers: {'Content-type': 'application/json; charset=UTF-8'}
            }
        )
            .then(response => {
                console.log("online=false: correcto")
            })
            .catch(async err =>{
                console.log("online=false: ERROR: ",err);
                await DetenerUbicacionOnline(id)
            });
    }

    function seleccion_ev(event){
        if(MostrarMicros){
            clearTimeout(idProceso)
            while(UbicGraf.length>0)
                EliminarUbicacionDelMApa(0)
            idProceso=setTimeout(EmpezaraMostarUbicaciones, tiempoSolicitud)
        }
        const seleccion=event.target.value
        IdLinea=seleccion
        DibujarLinea(seleccion)
    }
    
    
    function DibujarLinea(id) {
        const ruta=jsonLineas.find((lin)=>lin.id==id)
        if(ruta.path.length>=2){
            let ini=ruta.path[0]
            let fin=ruta.path[ruta.path.length-1]
            let wp=[]
            for (var i = 1; i < ruta.path.length-1; i++) {
                wp.push({location: (ruta.path[i].lat+', '+ruta.path[i].lng), stopover:false})
            }
            directionsService
                .route({
                    origin: ini,
                    destination: fin,
                    waypoints: wp,
                    travelMode: window.google.maps.TravelMode.DRIVING,
                    avoidTolls: false,
                    provideRouteAlternatives: false,
                })
                .then((response) => {
                    directionsRenderer.setDirections(response);
                })
                .catch((e) => console.log("Directions request failed due to ", e));
        }
    }

    async function EmpezaraMostarUbicaciones() {
        if(MostrarMicros){
            cant+=1
            await ActualizarUbicaciones()
            console.log(MostrarMicros, cant);
            document.getElementById("hh").textContent="Solicitudes: "+cant
            idProceso=setTimeout(EmpezaraMostarUbicaciones, tiempoSolicitud);
        }
        else{
            clearTimeout(idProceso)
            while(UbicGraf.length>0)
                EliminarUbicacionDelMApa(0)
        }
    }

    function Button_click(e) {
        if(IdLinea!=null){
            MostrarMicros=!MostrarMicros
            e.target.textContent=(MostrarMicros? "Ocultar": "Mostrar")+" Micros"
            if(MostrarMicros){
                tiempoSolicitud=eval(document.getElementById("inpMilsegSolic").value)
                SegUbicInact=eval(document.getElementById("inpSegInact").value)
                idProceso=setTimeout(EmpezaraMostarUbicaciones, tiempoSolicitud)
            }
        }
        else
            alert("selecciona una linea")
    }

    return(
        <div style={{display:"flex"}}>
            <div style={{width:'200px', backgroundColor: '#888'}}>
                <label>peticiones/Milseg </label>
                <input id="inpMilsegSolic" type="text"/><br />
                <label>Mostrar Ubicacion{"<"}seg</label>
                <input id="inpSegInact" type="text"/>
                <h4 id="hh" style={{textAlign:"center"}}>Solicitudes: {cant}</h4>
                <button onClick={Button_click} style={{width:'100%'}}>Mostrar Micros</button>
                <select id="select" onChange={seleccion_ev} defaultValue={'0'} style={{width:'100%'}}>
                    <option value="0" disabled>Selecciona una Linea...</option>
                </select>
            </div>
            <div id="map" style={{width:'100%', height:'100vh', backgroundColor: 'yellow'}}></div>
        </div>
    ) 
}