import React, { useEffect} from "react";
import { useHistory } from "react-router-dom";
import {urls} from "./Apis"

export default function Agregarlinea(){

   let history=useHistory()
   let map, directionsRenderer, directionsService, cant=0;
   
   useEffect(()=>{
      map = new window.google.maps.Map(document.getElementById("map"), {
         center: { lat: -17.78443767407429, lng: -63.18189476666437 },
         zoom: 11,
      });
      directionsService = new window.google.maps.DirectionsService();
      directionsRenderer = new window.google.maps.DirectionsRenderer({
          draggable: true,
          map,
      });
      const coord=[]
      map.addListener("click", (e) => {
         if(coord.length<2){
            coord.push({lat: e.latLng.lat(), lng: e.latLng.lng()})
            if(coord.length>1){
               directionsService
                     .route({
                        origin: coord[0],
                        destination: coord[coord.length-1],
                        travelMode: window.google.maps.TravelMode.DRIVING,
                        avoidTolls: false,
                        provideRouteAlternatives: false,
                     })
                     .then((response) => {
                        directionsRenderer.setDirections(response);
                     })
                     .catch((e) => window.alert("Directions request failed due to " + e));
            }
            else if(coord.length>2){
               coord.splice(0, coord.length-1)
            }
         }
            /*CODIGO BETA PARA PROGRAMAR EL BORRADO DE ALGUN WAYPOINT
            else if(valorActivo=="x"){
                const i=Object.keys(directionsRenderer.gm_bindings_.markerOptions)[0];
                console.log(i);
                console.log(directionsRenderer.gm_bindings_.markerOptions[i].ek['markers']);
                let mark=directionsRenderer.gm_bindings_.markerOptions[i].ek['markers']
                //mark[0].setPosition({lat: -17.738267, lng: -63.087960});
                mark[1].addListener("click", (e) => {
                    alert()
                })
            }*/
      })
   }, []);

   function Retroceder_click() {
      
   }

   function Avanzar_click() {

   }

   async function Guardar_click(e) {
      console.log(directionsRenderer);
      e.target.disabled=true;
      let puntos=[]
      if(directionsRenderer.directions){
         const resp=directionsRenderer.directions.request
         puntos.push({
            lat: resp.origin.lat? resp.origin.lat(): resp.origin.location.lat(), 
            lng: resp.origin.lng? resp.origin.lng(): resp.origin.location.lng()
         })

         if(resp.waypoints)
            resp.waypoints.forEach(w => puntos.push({lat: w.location.lat(), lng: w.location.lng()}));

         puntos.push({
            lat: resp.destination.lat? resp.destination.lat(): resp.destination.location.lat(), 
            lng: resp.destination.lng? resp.destination.lng(): resp.destination.location.lng()
         })
      }
      console.log("puntos a enviar:",puntos);
      if(puntos.length<2){
         alert("Selecciona 2 puntos!")
         e.target.disabled=false;
         return;
      }
      const datos = {
         "name": document.getElementById("inputNumLinea").value,
         "color": document.getElementById("inputColorLinea").value,
         "birthday": document.getElementById("inputFechaLinea").value,
         "path": puntos,
         "gremial_id": document.getElementById("inputIdSindLinea").value
      };
      await fetch(
         urls.Lineas
          ,{
              method: 'POST',
              body: JSON.stringify(datos),
              headers: {
                  'Content-type': 'application/json; charset=UTF-8'
              }
          }
      )
         .then(response => {
            alert("ENVIO CORRECTO: "+response);
            history.push("/inicioadmin")
         })
         .catch(err =>{
            alert("ENVIO ERRONEO: "+err);
            e.target.disabled=false;
         })
   }

    return(
        <div>
            <h2 style={{textAlign:"center"}}>Selecciona el punto Inicio y Fin en el Mapa</h2>
            <div id='map' style={{height:'80vh', width:'100vw', backgroundColor:'red'}}></div>
            <button onClick={Retroceder_click} disabled>Retroceder</button>
            <button onClick={Avanzar_click} disabled>Avanzar</button>
            <div>
               <input id="inputNumLinea" type="text" placeholder={79}/>
               <label> Numero </label>
               <br />
               <input id="inputColorLinea" type="text" placeholder={"rojo"}/>
               <label> Color </label>
               <br />
               <input id="inputFechaLinea" type="text" placeholder={"2000/01/01"}/>
               <label> Fecha de Creacion</label>
               <br />
               <input id="inputIdSindLinea" type="text" placeholder={2}/>
               <label> ID de Sindicato</label>
               <br />
               <button onClick={Guardar_click}>GUARDAR</button>
            </div>
        </div>
    ) 
}